<!--  -->
<template>
  <div class="km-page">
    <div class="km-title">
      <dxHeader>
        <div slot="right">
          <afIcon
            class="iconfont icon-share rightIcon"
            style="font-size:18px"
          />
        </div>
      </dxHeader>
    </div>
    <div>
      <dxScroll class="km-body">
        <div class="km-info">
          <div class="km-info-top">
            <div>
              
            </div>
            <div>
              <img src="https://picsum.photos/705" alt="" />
            </div>
          </div>
          <div class="km-info-middle">
            <div class="info-middle-left">
              <div> </div>
              <div>
                <div class="km-tag km-gray"></div>
                <div></div>
              </div>
            </div>
            <div class="info-middle-right">
              <div></div>
              <!-- <img src="https://picsum.photos/705" alt=""> -->
            </div>
          </div>
          <div class="km-info-person-info">
            <div>
              
            </div>
            <div>
              <afIcon class="iconfont icon-right " style="font-size:18px" />
            </div>
          </div>
          <div class="km-info-person-info-pingjia">
            <afIcon class="iconfont icon-star2" size="sIcon" />
            <span> 4.98</span>
            <div><span>9</span></div>
            <div><span>770</span></div>
            <div><span>558</span></div>
          </div>
          <div class="km-info-person-tab">
            <div class="doctor-tab">
              <div v-for="(tag, k) in tags" :key="k">
                {{ tag }}
              </div>
            </div>
          </div>
        </div>
        <div class="km-info-price">
          <div class="km-titletext"></div>
          <div>
            <div> 79  95</div>
            <afIcon class="iconfont icon-right" size="sIcon" />
          </div>
        </div>
        <div class="km-info-major">
          <div class="km-titletext"></div>
          <div class="major-tabs">
            <div v-for="(tab, k) in majorList" :key="k">{{ tab }}</div>
          </div>
          <div class="major-answer">
            <div class="answer-items">
              <div v-for="(v, i) in 2" :key="i">
                <div>
                  <img src="https://picsum.photos/700" />
                  <div></div>
                </div>
                <div>
                  102411121130,
                </div>
                <div>
                  1212 17:54
                </div>
              </div>
            </div>
          </div>
        </div>
      </dxScroll>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      tags: [
        "",
        "",
        "",
        "",
        ""
      ],
      majorList: [
        "7625",
        "571",
        "412",
        "138",
        "138"
      ]
    };
  },
  props: [],
  components: {},
  created() {},
  mounted() {},
  methods: {}
};
</script>
<style lang="scss" scoped>
.km-body {
  position: absolute;
  padding: 0 16px;
  left: 0;
  bottom: 0;
  overflow: hidden;
  right: 0;
  top: 64px;
  .km-info {
    .km-info-top {
      display: flex;
      > :first-child {
        font-size: 28px;
        font-weight: 530;
      }
      > :last-child {
        margin-left: auto;
        img {
          width: 60px;
          height: 60px;
          border-radius: 5px;
        }
      }
    }
    .km-info-middle {
      font-size: 14px;
      display: flex;
      margin-bottom: 10px;
      color: #333;
      align-items: center;
      .info-middle-left {
        > :last-child {
          margin-top: 5px;
          display: flex;
          align-items: center;
          div {
            margin-right: 10px;
          }
        }
      }
      .info-middle-right {
        margin-left: auto;
        background-color: #f9f9f9;
        margin-right: 5px;
        text-decoration: solid;
        color: #31b794;
        text-decoration: underline;
        border-radius: 10px;
        padding: 3px 10px;
        font-size: 14px;
      }
    }
    .km-info-person-info {
      font-size: 14px;
      align-items: center;
      display: flex;
      > :first-child {
        text-overflow: -o-ellipsis-lastline;
        overflow: hidden;
        margin-right: 3px;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        color: #333;
      }
      > :last-child i {
        color: #999;
        font-size: 14px !important;
        margin-left: auto;
      }
    }
    .km-info-person-info-pingjia {
      margin-top: 20px;
      display: flex;
      i {
        font-size: 20px !important;
        color: #ea9107;
      }
      span {
        margin-right: 2px;
        font-size: 20px;
        color: #000;
      }
      > :nth-child(2) {
        font-weight: lighter;
        color: #ea9107;
      }
      div {
        margin-left: 12px;
        font-size: 12px;
        color: #666;
      }
      > :nth-child(2) {
        font-size: 20px;
        font-weight: lighter;
        color: #ea9107;
      }
    }
    .km-info-person-tab {
      margin-top: 24px;
      .doctor-tab {
        display: flex;
        flex-wrap: wrap;
        div {
          margin-right: 5px;
          border-radius: 5px;
          background-color: #fafafa;
          font-size: 12px;
          padding: 1px 1px;
          color: #ea9107;
        }
      }
    }
  }
  .km-info-price {
    display: flex;
    align-items: center;
    margin-top: 24px;
    > :first-child {
      @extend .km-titletext;
    }
    > :last-child {
      font-size: 14px;
      display: flex;
      align-items: center;
      color: #31b794;
      margin-left: auto;
      i {
        font-size: 12px !important;
      }
      div {
        text-decoration: underline;
      }
    }
  }
  .km-info-major {
    margin-top: 24px;
    > :first-child {
      margin-bottom: 24px;
    }
    .major-tabs {
      display: flex;
      flex-wrap: wrap;
      div {
        font-size: 12px;
        margin-right: 8px;
        height: 16px;
        margin-bottom: 12px;
        line-height: 16px;
        color: #666;
        padding: 5px 8px;
        border-radius: 5px;
        background-color: #f6f6f6;
      }
    }
    .major-answer {
      margin-top: 16px;
      .answer-items {
        > div {
          padding: 16px 0;
          position: relative;
          > :first-child {
            display: flex;
            font-size: 14px;
            align-items: center;
            img {
              width: 20px;
              height: 20px;
              border-radius: 50%;
            }
            div {
              font-weight: bold;
              letter-spacing: 1px;
              margin-left: 5px;
            }
          }
          > :nth-child(2) {
            margin-top: 10px;
            font-size: 14px;
            text-overflow: -o-ellipsis-lastline;
            overflow: hidden;
            margin-right: 3px;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 3;
            -webkit-box-orient: vertical;
            color: #333;
          }
          > :last-child {
            font-size: 12px;
            margin-top: 12px;
            color: #d0d0d0;
          }
        }
        > div::after {
          position: absolute;
          box-sizing: border-box;
          content: " ";
          pointer-events: none;
          right: 12px;
          bottom: 0;
          left: 12px;
          border-bottom: 1px solid #eee;
        
        }
      }
    }
  }
}
.km-tag {
  padding: 2px;
  font-size: 12px;
}
.km-gray {
  background-color: #eee;
}
.km-titletext {
  font-size: 20px;
}
</style>



