<template>
  <div id="app">
    <router-view />
    <Tabbar v-show="tabBarShow" class="tabbar"></Tabbar>
    <adsToast v-if="showAdv" v-bind:showAdv.sync='showAdv'></adsToast>
  </div>
</template>

<script>
import Tabbar from "@/components/dxTabbar";
import adsToast from "@/pages/adsToast/adsToast";
export default {
  name: "App",
  data() {
    return {
      tabBarShow: true,
      showAdv:true
    };
  },
  components: {
    Tabbar,
    [adsToast.name]:adsToast
  },
  watch: {
    $route: {
      immediate: true,
      handler: function(to, from) {
        console.log("to.path: " + to.path);
        // Vue.prototype.$fromPage = from.path;
        //tabbar
        if (
          to.path == "/" ||
          to.path == "/baike" ||
          to.path == "/findPage" ||
          to.path == "/home"
        ) {
          this.tabBarShow = true;
        } else {
          this.tabBarShow = false;
        }
      }
    }
  }
};
</script>

<style>
@font-face {
  font-family: "fangping";
  src: url("~@/assets/fonts/fangping.ttf");
  /* font-family: "Open Sans", -apple-system, BlinkMacSystemFont, "Helvetica Neue",
    Helvetica, Segoe UI, Arial, Roboto, "PingFang SC", "miui",
    "Hiragino Sans GB", "Microsoft Yahei", sans-serif; */
}
* {
  -webkit-touch-callout: none; /**/
  -webkit-user-select: none; /*webkit*/
  -khtml-user-select: none; /**/
  -moz-user-select: none; /**/
  -ms-user-select: none; /*IE10*/
  user-select: none;
}
input,
textarea {
  -webkit-user-select: auto; /*webkit*/
  margin: 0px;
  padding: 0px;
  outline: none;
}

html,
body {
  font-size: 16px;
  padding: 0;
  margin: 0;
  -webkit-overflow-scrolling: touch;
  color: #000;
}
::-webkit-scrollbar {
}
::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background-color: transparent;
}
/* ::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
  background: #ededed;
  border-radius: 5px;
} */
.shadow {
  box-shadow: 0px 2px 2px #eee;
}
.hidden {
  overflow: hidden;
}
.van-toast__text {
  color: #fff;
}
.tabbar {
  z-index: 999;
}
</style>



