<!--   -->
<template>
  <van-action-sheet
    v-model="show.visable"
    :actions="actions"
    @select="onSelect"
    cancel-text=""
    close-on-click-action
  />
</template>

<script>
import { takePhoto, takefromgalery } from "@u/android.js";
export default {
  name: "picActionSheet",
  props: ["show"],
  data() {
    return {
      actions: [
        { name: "", index: 1 },
        { name: "", index: 2 },
        { name: "", index: 3 }
      ]
    };
  },
  methods: {
    onSelect(item) {
      let that = this;
      switch (item.index) {
        case 1:
          this.$emit("change", item);
          break;
        case 2:
          takePhoto(res => {
            this.$emit("fileChangeSuccess", res);
          });
          break;
        case 3:
          takefromgalery(res => {
            this.$emit("fileChangeSuccess", res);
          });
          break;
      }
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.dx-header {
  position: fixed;
  width: 100%;
  z-index: 999;
}
</style>



