import $axios from 'axios';
import qs from 'qs';
/**
 * @param {String} url  
 * @param {any} data 
 * @param {Object} option axios
 * @param {String} option.original axios
 * @param {String} option.qs qs.stringify
 */

/**
 * 
 * get    this.$http.get(url,data)
 * post   this.$http.post(url,data,{qs:true}) qs data FormData 
 * put    this.$http.put(url,data,{qs:true}) putpost_method=put
 * delet  this.$http.delet(url,data)
 *  this.$http.get(url,data,{original:true}) 
 */
export const get = (url, data, option) => {
  //option.original true 
  if (option instanceof Object && option.original) {
    delete option.original;
    return $axios.get(url, data, option);
  } else if (option instanceof Object && option.qs) {
    delete option.qs;
    data = qs.stringify(data);
  }
  return $axios.get(url, {
    params: data,
    ...option
  });
};

export const post = (url, data, option) => {
  if (option instanceof Object && option.original) {
    delete option.original;
  } else if (option instanceof Object && option.qs) {
    data = option.qs ? qs.stringify(data) : data;
    delete option.qs;
  }
  return $axios.post(url, data, option);
};

export const put = (url, data, option) => {
  if (option instanceof Object && option.original) {
    delete option.original;
    return $axios.put(url, data, option);
  } else if (option instanceof Object && option.qs) {
    data = option.qs ? qs.stringify(data) : data;
    delete option.qs;
  }
  // //putpost_method=put
  // if (url.includes('?')) {
  //   url = url + '&_method=put';
  // } else {
  //   url = url + '?_method=put';
  // }
  return $axios.put(url, data, option);
};
export const delet = (url, data, option) => {
  if (option instanceof Object && option.original) {
    delete option.original;
    return $axios.delete(url, data, option);
  } else if (option instanceof Object && option.qs) {
    delete option.qs;
    data = qs.stringify(data);
  }
  return $axios.delete(
    url,
    {
      params: data
    },
    option
  );
};
const $http = {
  get,
  post,
  put,
  delete: delet
};
export default $http;



